<template>
  <div class="contens " :style="'height:'+curHeight">
    <h1><span></span>租车到期后如何还车？</h1>
    <ul class="List">
      <li>
        <div class="imgDiv">
          <img src="../../../assets/feedbacksFlow/m19.png" alt="">
        </div>
      </li>
      <li>
        <div class="header2">
          <div class="imgh1"><img src="../../../assets/feedbacksFlow/m3s.png" alt=""></div>
          还车点还车
          <div class="imgh2"><img src="../../../assets/feedbacksFlow/m3.png" alt=""></div>
        </div>
      </li>
      <li>
        <div class="textCent">
          <p><i></i>在订单详情内查看还车点。</p>
          <p><i></i>到还车点后给商家展示订单详情内的还车码。</p>
          <p><i></i>商家扫还车码后即可还车成功。</p>
          <p><i></i>若逾期还车，请与商家进行沟通结算。</p>
          <p><i></i>还车成功后，若存在押金，则在e换电APP-我-钱包-押金处退还即可。</p>
        </div>
      </li>

    </ul>
  </div>
</template>
<script>
  export default {
    components:{
    },
    data() {
      return {
        active: 0,
        curHeight:"100%"
      }
    },
    mounted(){
      var h = document.documentElement.clientHeight || document.body.clientHeight;
      this.curHeight =h-90+"px"; //减去页面上固定高度height
    },
    methods:{

    },
  }
</script>

<style scoped lang="scss">
  .Fixs{
    position: fixed;
    left: 0;
    top: 88px;
    height: 100%;
    width: 100%;
  }
  .List{
    padding-bottom: 80px;
    background: #ffffff;
    li{
      width: 100%;
      padding: 0 20px;
      box-sizing: border-box;
    }
  }
  .textCent{
    display: inline-block;
    margin: 0 auto;
    p{
      font-size:26px;
      font-family:PingFangSC;
      font-weight:400;
      color:rgba(72,72,72,1);
      line-height:36px;
      margin-bottom: 20px;
      text-align: left;
      position: relative;
      padding-left: 26px;

      i{
        width:8px;
        height:8px;
        display: inline-block;
        background:rgba(165,165,165,1);
        border-radius: 4px;
        position: absolute;
        left: 0px;
        top: 14px;
      }
    }


  }
  .header2{
    height: 48px;
    display:inline-block;
    margin: 0 auto;
    margin-top: 52px;
    margin-bottom: 40px;
    font-size:34px;
    font-family:PingFangSC;
    font-weight:600;
    color:rgba(51,51,51,1);
    line-height:48px;
    position: relative;
    div{
      width:102px;
      height:12px;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .imgh1{
      position: absolute;
      left: -122px;
      top: 0;
    }
    .imgh2{
      position: absolute;
      right: -128px;
      top: 0;
    }
  }
  .imgDiv{
    height: 220px;
    width: 300px;
    margin: 0 auto;
    margin-top: 42px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .contens{
    margin-top: 58px;
    h1{
      width:100%;
      height:48px;
      font-size:34px;
      font-family:PingFangSC;
      font-weight:600;
      color:rgba(51,51,51,1);
      line-height:48px;
      padding-left: 76px;
      position: relative;
      text-align: left;
      span{
        position: absolute;
        left: 52px;
        top: 8px;
        width:8px;
        height:32px;
        background:rgba(255,141,89,1);
      }
    }
  }
</style>



